const profiles = [
  {
    title: 'iOS14/iPadOS14',
    description: 'iOS 14 和 iPadOS 14 描述性文件',
    downloadLink: 'https://cdn.kejiweixun.com/share/iOS_iPadOS_14_AppleSeed_Profile.mobileconfig',
    logo: ios
  },
  {
    title: 'Big Sur',
    description: 'macOS Big Sur 描述性文件',
    downloadLink: 'https://cdn.kejiweixun.com/share/macOSDeveloperBeta16AccessUtility.dmg',
    logo: macos
  },
  {
    title: 'watchOS 7',
    description: 'wathcOS 7 描述性文件',
    downloadLink: 'https://cdn.kejiweixun.com/share/watchOS_7_Beta_Profile.mobileconfig',
    logo: watchos
  },
  {
    title: 'tvOS 14',
    description: 'tvOS 14 描述性文件',
    downloadLink: 'https://cdn.kejiweixun.com/share/tvOS_14_Beta_Profile.mobileconfig',
    logo: tvos
  },
];

export default ({ data }) => {
  const [updateInfo, setupdateInfo] = useState([])
  useEffect(() => {
    const updateURL = "https://api.kejiweixun.com/api/apple_os_live_update"
    function fetchData() {
      fetch(updateURL)
        .then(res => res.json())
        .then(res => {
          let list = []
          for (let i of res) {
            if (
              i.title.indexOf("iOS") === 0 ||
              i.title.indexOf("watchOS") === 0 ||
              i.title.indexOf("macOS") === 0 ||
              i.title.indexOf("tvOS") === 0 ||
              i.title.indexOf("iPadOS") === 0
            ) {
              let time = new Date(Date.parse(i.isoDate)).toLocaleString()
              i.time = time
              list.push(i)
            }
          }
          setupdateInfo(list)
        })
    }
    fetchData()
  }, [])

  const osUpdate = updateInfo.length ? (
    updateInfo.map(item => {
      return (
        <li key={item.title}>
          <span>{item.title}</span>
          <span>时间: {item.time}</span>
        </li>
      )
    })
  ) : (
      <>
        <br />
        <p>加载中...</p>
        <br />
      </>
    );

  return (
    <Layout>
      <Helmet>
        <title>苹果系统描述性文件下载</title>
        <meta
          name="description"
          content="在这里可以下载 iOS 14 描述性文件, macOS Big Sur 描述性文件, iPadOS 14 描述性文件, watchOS 7 描述性文件, tvOS 14 描述性文件, 以及用来屏蔽系统更新的描述性文件, 还可以查看它们的实时更新记录."
        />
      </Helmet>
      <h1 css={hStyle}>苹果描述性文件下载</h1>
      <Img fluid={data.file.childImageSharp.fluid} />
      <ul css={iconStyle}>
        {
          profiles.map(profile => {
            return (
              <li>
                <a href={profile.downloadLink}>
                  <figure>
                    <img src={profile.logo} alt={profile.description} />
                    <figcaption>{profile.title}</figcaption>
                  </figure>
                </a>
              </li>
            )
          })
        }
      </ul>
      <section css={tipStyle}>
        <h3>
          <span role="img" aria-label="star emoji">
            ✨
          </span>
          提示:
        </h3>
        <ul>
          <li>在 iPhone 或 iPad 打开这个网页, 点击图标即可下载;</li>
          <li>都是用来升级"开发者测试版"的文件;</li>
          <li>iPadOS 和 iOS 的描述性文件是相同的;</li>
          <li>屏蔽系统更新的描述文件其实是 tvOS 12 的描述性文件;</li>
        </ul>
      </section>
      <section css={updateStyle}>
        <h3>实时更新记录:</h3>
        <ul>{osUpdate}</ul>
      </section>
      <ISSO title="苹果系统描述性文件下载" slug={`ios-update/`} />
    </Layout>
  )
}

export const data = graphql`
  query {
    file(name: { eq: "idevices" }) {
      childImageSharp {
        fluid(maxWidth: 800) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`
